@import './utils/mixins.scss';
@import './utils/variables';

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
html {
  overflow-x: hidden;
}

body {
  margin: 0;
  min-height: 100vh;
  position: relative;
  background-image: url('./images/body__background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  font-family: 'Ubuntu', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: white;
}

html.no-scroll,
body.no-scroll {
  overflow-y: hidden;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.7);
}
body::-webkit-scrollbar-thumb {
  background-color: $color-white;
  border-radius: 20px;
}

h1,
h2,
h3 {
  margin: 0;
  font-family: 'Pacifico', cursive;
  font-size: 38px;
  font-weight: 400;
  text-align: center;
}

body::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0;
}

button {
  display: block;
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
}

img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
}

@include mediaQuery(smallDesktop) {
  h1,
  h2,
  h3 {
    font-size: 32px;
  }
}

@include mediaQuery(tablet) {
  h1,
  h2,
  h3 {
    font-size: 26px;
  }
}

@include mediaQuery(phone) {
  h1,
  h2,
  h3 {
    font-size: 20px;
  }
}

